$maincolor: blue;
$mainborder: 1px solid $maincolor;
*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.welcome{
  text-align: center;
  background-color: $maincolor;
  padding: 3em 0;
  color: white;
}

.access-forms{
  width: 35%;
  float: right;
  text-align: center;
  margin: 1em;
  .login-form{
    display: block;
  }//.login-form
  .signup-form{
    display: none;
  }//.signup-form
  .signup-form, .login-form{
    padding: 1em 0.5em;
    border: $mainborder;
    span{
      button{
        background-color: transparent;
        text-decoration: none;
        color: $maincolor;
        &:hover{
          color: grey;
        }//hover
      }//a
    }//span
  }//.signup-form, .login-form
  input{
    width: 100%;
    padding: 1em;
    margin: 0.5em 0;
  }//input
  input[type="button"]{
    display: inline-block;
    width: auto;
    clear: both;
    padding: 0.5em 2em;
    color: white;
    background-color: $maincolor;
    border: $mainborder;
    &:hover{
      background-color: white;
      color: $maincolor;
      cursor: pointer;
    }//hover
  }//input[type=button]
}

